th,
td {
  border-right: 1px solid black; /* Add a vertical line to the right of each th/td */
  white-space: nowrap; /* Prevent text from wrapping */
  padding: 8px; /* Add padding for better spacing */
}

table {
  border-collapse: collapse; /* Collapse table borders */
}

tr {
  border-bottom: 1px solid black; /* Add a line before each horizontal line (row) */
}

.column-id{
  min-width: 10px;
}

.column-numbersm{
  min-width: 70px;
}

.notification {
  position: fixed;
  bottom: 10px;
  right: 10px;
  background-color: green;
  color: white;
  padding: 10px;
  border-radius: 5px;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.notification.show {
  opacity: 1;
}